export default {
	onLoad(option) {
		this.id = option.id;
		this.getData();
	},
    data() {
        return {
			id:',',
			dataList:[],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500
        }
    },
	methods:{
		getData(){
			let url = 'http://yxx.h-etrip.com:9092/etrip/api/app/et/article/cultures/' + this.id;
			uni.request({
			    url: url,
			    success: (res) => {
			        this.dataList = res.data.content;
			    }
			});
		},
		getImgUrl(data) {
		     return this.getSrc(data);
		},
	}
}
